import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Accounts from './views/Accounts.vue'
import Profiles from './views/Profiles.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/accounts',
      name: 'accounts',
      component: Accounts
    },
    {
      path: '/profiles',
      name: 'profiles',
      component: Profiles
    }
  ]
})
